<template>
    <div class="scrollBox">
        <span id="btn" @click.stop.prevent="fackTop" :class="isShow?'scrollStart':'scrollEnd'"></span>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                isShow: false,
                timer: null
            }
        },
        mounted(){
            window.onscroll= ()=>{
                var scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
                if(scrollTop>500){
                        this.isShow = true
                } else{
                    this.isShow = false;
                }
            }
        },
        methods: {
            fackTop(){
                clearInterval(this.timer);
                var start=document.documentElement.scrollTop || document.body.scrollTop;
                var dis= 0-start;
                var count=Math.floor(1000/30);//走的次数
                var n=0;
                this.timer=setInterval(()=>{
                    n++;
                    var a= 1 - n / count;
                    var cur=start + (1-Math.pow(a,3)) * dis;
                    document.documentElement.scrollTop = document.body.scrollTop=cur;
                    if(n==count){
                        clearInterval(this.timer);
                    }
                },30);
            }
        }
    }
</script>
<style>
    #btn{
        width: 100px;
        height: 100px;
        display: block;
        border-radius: 50%;
        background-image: url('../assets/images/scroll_btn.gif');
        background-repeat: no-repeat;
        background-size: cover;
        position:fixed;
        top:-100px;
        left:80%;
    }
    .scrollStart{
        animation: startScroll ease-in .5s;
        animation-fill-mode: forwards;
        display: block !important;
    }
    .scrollEnd{
        animation: endScroll ease-in .5s;
        animation-fill-mode: forwards;
    }
    @keyframes startScroll {
        0%{
            top: -100px;
        }
        100%{
            top: 70%;
        }
    }
    @keyframes endScroll {
        0%{
            top: 70%;
        }
        100%{
            top: -100px;
        }
    }
</style>




