<template>
  <div class="audioMusic">

    <div class="searchMusic">
      <img src="../assets/images/qqmusic.png" alt="">
      <input type="text" placeholder="请输入喜欢的歌曲~" v-model="audioData.seachVal" @keyup.enter="searchMusic">
    </div>
    <div v-if="songList.length > 0">
      <!--  -->
      <audio :src="songList[audioData.index].url" ref="player" id="player" type="audio/*" class="player">
        <source :src="songList[audioData.index].url"  type="audio/*">
      </audio>
      <!-- <div class="volume">
        音量：
        <div class="volume_box">
          <div></div>  
        </div>
      </div>
      <div class="pattern">
        <span @click="orderSong">顺序</span>
        <span @click="randomSong">随机</span>
      </div> -->
      <!-- <div class="Songlist">
        SongList
      </div> -->

      <div class="cus_play">
        <div class="song_drag">
          <div class="song_img" :style="!audioMessage.paused?'':'animation: song_img 20000ms infinite linear;'">
            <img :src="songList[audioData.index].headportrait || '../assets/images/error_img.jpg'" alt="">
          </div>
          <div class="cus_cont">
            <div class="song_text">
              <p>{{songList[audioData.index].subtitle}}</p>
              <p>{{songList[audioData.index].singer}}</p>
            </div>
            <cusProgress 
              :height="4"
              :width="200"
              :isLevel="true"
              :progressPellet="progressPellet" 
              :playerProgress="playerProgress"
              @progressStart="progressStart"
              @progressMove="progressMove"
              @progressDown="progressDown"
              ></cusProgress>
            <p class="cus_timer">
              <span>{{audioMessage.currentTime | filterTimer}}</span>
              <span>{{audioMessage.duration | filterTimer}}</span>
            </p>
            <div class="song_container">
              <span class="prev_btn"  @click="prevSong(audioData.index)">
                <img src="../assets/images/prev_icon.png" alt="" width="20">
              </span>
              <span class="prev_btn" @click="stopSong">
                <img v-if="!audioMessage.paused" src="../assets/images/audio_play.png" alt="" width="20">
                <img v-else-if="audioMessage.paused" src="../assets/images/audio_pause.png" alt="" width="20">
              </span>
              <span class="prev_btn" @click="nextSong(audioData.index)">
                <img src="../assets/images/next_icon.png" alt="" width="20">
              </span>
              <span @click="isanimate = !isanimate">{{isanimate?'收起':'展开'}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else >
      <p class="song_msg"></p>
        <div class="audio_error">
          <img src="../assets/images/back_img.jpg" alt="" width="420" height="200">
        <p>
          <img src="../assets/images/scroll_btn.gif" alt="" width="100" height="100" style="border-radius: 50%;">
          <span>歌曲搬运工正在赶到...</span>
        </p>
      </div>
    </div>
    <!-- 列表 -->
    <div>
        <div class="Songlist" :class="isanimate?'addanimate':'deleteanimate'">
          <ul class="">
            <li v-for="(item,keys) in songList" class="pointer" :class="audioData.index == keys?'song_active':''" @click="songActive(keys)">
              <img :src="item.headportrait" alt="" width="100" height="100">
              <div>
              <p>
                <span>{{item.subtitle}}</span>
                  <span>{{item.introduction}}</span>
              </p>
              <p>
                  <span>{{item.singer}}</span>
                  <span>{{item.name}}</span>
              </p>
              </div>
            </li>
          </ul>
          <div class="songRight" :class="isanimate?'show_allImg':'hide_allImg'">
            <p class="song_msg">
              当前歌曲的keys值：{{audioData.index}}
              当前页数：{{audioData.pageIndex}}
              总页数：{{audioData.totalNum}}
              总歌曲：{{audioData.totalSize}}
            </p>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
  import { songApi } from '../api/songApi.js';
  import cusProgress from './App.vue';
  export default {
     components: {
      cusProgress
    },
    data(){
      return {
        songList:[],
        audioData:{
          index: 0,
          pageIndex: 1,
          pageSize: 5,
          seachVal: '',
          totalNum:'', //总页数
          totalSize: '' //总曲
        },
        playerProgress: '',
        progressPellet: '',
        player: null,
        audioMessage:{
          // audioTracks: '', //返回表示可用音频轨道的 AudioTrackList 对象。※
          autoplay: false, // 设置或返回是否在就绪（加载完成）后随即播放音频。※
          controls: false, //是否显示控件 ※
          TimeRanges: '', //表示音频已经缓存的部分 ※
          currentTime: '', //设置或返回音频中的当前播放位置（以秒计）。※
          defaultMuted: '', //设置或返回音频默认是否静音。※
          defaultPlaybackRate: '', //设置或返回音频的默认播放速度。※
          duration: '', //返回音频的长度。※
          ended: '', //返回音频的播放是否已结束。※
          error: '', //返回表示音频错误状态的 MediaError 对象。※
          loop: '', // 设置或返回音频是否应在结束时再次播放。※
          mediaGroup: '', //设置或返回音频所属媒介组合的名称。※
          muted: '', //设置或返回是否关闭声音。※
          networkState: '', //返回音频的当前网络状态。※
          paused: '', // 设置或返回音频是否暂停。※
          playbackRate: '', //设置或返回音频播放的速度。※
          played: '', //返回表示音频已播放部分的 TimeRanges 对象。※
          preload: '', //设置或返回音频的 preload 属性的值。※
          readyState: '', //返回音频当前的就绪状态。※
          src: '', //设置或返回音频的 src 属性的值。※
          textTracks: '', //返回表示可用文本轨道的 TextTrackList 对象。※
          volume: '', //设置或返回音频的音量。※
        },
        progressleft:'',
        isSearch: true,
        isanimate: false
      }
    },
    mounted(){
      this.getSongList();
    },
    filters: {
      filterTimer(timer){
        let min = 60;
        let mm =timer / 60 | 0;
        let ss =timer % 60 | 0;
        return (String(mm).length < 2?'0'+mm:mm) + ':' + (String(ss).length < 2?'0'+ss:ss);
      }
    },
    computed: {
      currentTime(){
        return this.audioMessage.currentTime;
      },
      search(){
        return this.audioData.searchVal
      }
    },
    watch: {
      currentTime(newVal,oldVal){
        // this.playerProgress = newVal / this.audioMessage.duration * 100 + '%'; //获取到当前的进度

        // this.progressPellet = newVal / this.audioMessage.duration * 100 + '%';
      },
      search(newVal,oldVAl){
        if(newVal !=oldVal){
          this.isSearch = true;
        }else{
          this.isSearch = false;
        }
      }
    },
    methods:{
      getSongList(){
        let bodys = {};
        bodys.date = new Date().getDate();
        bodys.pageIndex = this.audioData.pageIndex;
        bodys.pageSize = this.audioData.pageSize;
        bodys.seachVal = this.audioData.seachVal;
        this.songList = [];
        songApi.songList(bodys).then( res=>{
          if(res.code == 0){
            let list = res.data.song.list;
            this.audioData.totalNum =  Math.ceil(res.data.song.totalnum / this.audioData.pageSize);  
            this.audioData.totalSize = res.data.song.totalnum;
            for(let i =0;i < list.length; i++){
              let name = '';
              for(let n = 0;n<list[i].singer.length;n++){
                name+='/'+list[i].singer[n].name
              }
              this.songList.push({
                'headportrait':`http://y.gtimg.cn/music/photo_new/T002R300x300M000${list[i].album.mid}.jpg?max_age=2592000`, //头像
                'singer':name.substring(1,name.length), //歌手显示一个
                'name':list[i].name, //歌曲
                'subtitle':list[i].subtitle, //歌曲title
                'introduction':list[i].album.name, //简介
                'url':``, //歌曲URL
                'Lyric': '', //歌词
                'mid': list[i].mid
              })
            };
            if(this.songList.length > 0){
              this.getsongUlr(0);
              this.initAudio();
            }
          };
        });
      },
      getsongUlr(index){
        let bodys = {};
        bodys.mid = this.songList[index].mid;
        songApi.getSongUlr(bodys).then( res =>{
          if(res.code == 0){
            this.songList[index].url = `http://dl.stream.qqmusic.qq.com/${res.data.items[0].filename}?vkey=${res.data.items[0].vkey}&guid=9758620110&uin=0&fromtag=66`;
          };
        });
      },
      initAudio(){
        this.$nextTick(()=>{
          this.player = this.$refs.player;
          this.player.addEventListener("canplaythrough",()=>{

            for(let i in this.audioMessage){
              this.audioMessage[i] = this.player[i]
            }
            if((this.audioMessage.networkState == 1 || this.audioMessage.networkState == 2 || this.audioMessage.networkState == 3) && this.player.src !=''){
              this.player.play();
            }
          });
          this.playSong();
        })
      },
      // 上一首
      prevSong(){
        this.progressStart();
        if(this.audioData.index < 1){
          this.audioData.index = 0;
          if(this.audioData.pageIndex <= 1){
            this.audioData.pageIndex = 1;
          }else{
            this.audioData.index = this.audioData.pageSize - 1;
            this.audioData.pageIndex--;
            this.songList = [];
            this.getSongList();
          }
        }else{
          this.audioData.index--;
          this.getsongUlr(this.audioData.index);
          if(this.songList[this.audioData.index].url !=''){
            if((this.audioMessage.networkState == 1 || this.audioMessage.networkState == 2 || this.audioMessage.networkState == 3) && this.player.src !=''){
              if(this.audioData.index != 0 && this.audioData.pageIndex == 1){
                this.player.load();
                this.player.play();
              }else{
                // 没有更多了...
              } 
            }
          }
        }
      },
      playSong(){
        this.player.addEventListener('ended',()=>{
          this.nextSong();
        });

        this.player.addEventListener('timeupdate',()=>{
          this.audioMessage.currentTime =this.player.currentTime; //获取当前播放时间
          this.playerProgress = this.player.currentTime / this.audioMessage.duration * 100 + '%'; //获取到当前的进度
          this.progressPellet = this.player.currentTime / this.audioMessage.duration * 100 + '%'; //
        });
      },
      progressStart(){
        this.player.pause();
        // this.progressMove();
      },
      progressMove(item){
        this.progressPellet = item.progressPellet;
        this.player.currentTime = this.audioMessage.duration * item.currentTime;
      },
      progressDown(item){
        this.progressPellet = item.progressPellet;
        this.player.currentTime = this.audioMessage.duration * item.currentTime;
      },
      // 暂停
      stopSong(){
        this.audioMessage.paused = !this.audioMessage.paused;
        if(!this.audioMessage.paused){
          this.player.pause();
        }else{
          this.player.play();
        }
      },
      // 下一首
      nextSong(){
        this.progressStart();
        if(this.audioData.index >= this.audioData.pageSize - 1){
          this.audioData.index = 0;
          if(this.audioData.pageIndex >= this.audioData.totalNum){
            this.audioData.pageIndex = this.audioData.totalNum;
          }else{
            console.log(`第${this.audioData.pageIndex + 1}页`)
            this.audioData.pageIndex++;
            this.songList = [];
            this.getSongList();
          }
        }else{
          if(this.audioData.index + ( (this.audioData.pageIndex - 1) * this.audioData.pageSize) < this.audioData.totalSize){ 
            this.audioData.index++;
            this.getsongUlr(this.audioData.index);
            if(this.songList[this.audioData.index].url !=''){
              if((this.audioMessage.networkState == 1 || this.audioMessage.networkState == 2 || this.audioMessage.networkState == 3) && this.player.src !=''){
                this.player.load();
                this.player.play();
              }
            }
          }else{
            console.log('找不到歌曲~')
          }
        }
      },
      // 顺序
      orderSong(){
        // this.player.ended
      },
      // 随机
      randomSong(){

      },
      searchMusic(){
        if(this.isSearch){
           this.getSongList();
        }else{
          return false;
        }
      },
      songActive(index){
        this.audioData.index = index;
        this.getsongUlr(this.audioData.index);
        if(this.songList[this.audioData.index].url !=''){
          if((this.audioMessage.networkState == 1 || this.audioMessage.networkState == 2 || this.audioMessage.networkState == 3) && this.player.src !=''){
            this.player.load();
            this.player.play();
          }
        }
      }
    }
  }
</script>
