import jsonp from '../common/js/jsonp'
import {commonParams,options} from './config'
import $ from 'jquery';

export const songApi = {
    songList(bodys){
        var random = `MusicJsonCallback${(Math.random()+"").replace("0.","")}`;
        var url ='https://c.y.qq.com/soso/fcgi-bin/client_search_cp';
        var data = Object.assign({},commonParams,{
            ct: bodys.date,
            qqmusic_ver: 1298,
            new_json: 1,
            remoteplace: 'txt.yqq.center',
            searchid: 46763926084942305,
            t: 0,
            aggr: 1,
            cr: 1,
            catZhida: 1,
            lossless: 0,
            flag_qc: 0,
            p: bodys.pageIndex, //页数
            n: bodys.pageSize, //每页显示个数
            w: bodys.seachVal || 'acg', //关键词
            loginUin: 0,
            hostUin: 0,
            notice: 0,
            platform: 'yqq',
            needNewCode: 0
        });
        options.prefix = random;
        return jsonp(url,data,options);
    },
    getSongUlr(bodys){
        var random = `MusicJsonCallback${(Math.random()+"").replace("0.","")}`;
        var url ='https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg';
        
        var data = Object.assign({},commonParams,{
            g_tk: 5381,
            loginUin: 0,
            hostUin: 0,
            notice: 0,
            platform: 'yqq',
            needNewCode: 0,
            cid: 205361747,
            uin: 0,
            songmid: bodys.mid,
            callback: random,
            filename: `C400${bodys.mid}.m4a`,
            guid: 9758620110
        })
        options.prefix = random;
        return jsonp(url,data,options)
    }
}



