<template>
  <div class="cus_progress" ref="cus_progress" :style="`width:'${width}px;height:${height}px`">
      <div class="progress_pellet" ref="progress_pellet" :style="isLevel?progressPellet?`left:${ width * parseFloat(progressPellet) / 100  - 5}px`:'':progressPellet?`top:${height * parseFloat(progressPellet) / 100 - 5}px`:''"></div>
    <div class="progress_bar" :style="playerProgress?`width:${playerProgress}`:''"></div>
  </div>
</template>
<script>
  export default {
    props: ['width', 'height', 'isLevel', 'playerProgress', 'progressPellet'],
    mounted(){
      this.$nextTick(()=>{
        this.$refs.progress_pellet.onmousedown =(ev)=>{
          var ev = ev || event;
          let x, y;
          if(this.isLevel){
            x = ev.clientX - this.$refs.progress_pellet.offsetLeft;
          }else{
            y = ev.clientY - this.$refs.progress_pellet.offsetTop;
          }
          
          this.$emit('progressStart',()=>{
            // 用于处理播放过程中播放器是否暂停
          })
          document.onmousemove=(ev)=>{
            var ev = ev || event;
            let progressPellet, currentTime;
            if(this.isLevel){
              var oLeft = ev.clientX - x;
              progressPellet = oLeft / this.width * 100 + '%';
              currentTime = oLeft / this.width;

              if(oLeft >= this.width){
                oLeft = this.width;
                return;
              }
            }else{
              var oTop = ev.clientY - y;
              progressPellet = oTop / this.height * 100 + '%';
              currentTime = oTop / this.height;
              if(oTop >= this.height){
                oTop = this.height;
                return;
              }
            };
            
            this.$emit('progressMove',{
                progressPellet: progressPellet,
                currentTime: currentTime
            });
            
          }
					  //防止选择内容--当拖动鼠标过快时候，弹起鼠标，bar也会移动，修复bug
            window.getSelection ? window.getSelection().removeAllRanges() : document.selection.empty();
        };
        document.onmouseup=()=>{
          document.onmousemove=null;
        }
        this.$refs.cus_progress.onmousedown =(ev)=>{
          var ev = ev || event;
          let progressPellet, currentTime;
          let x , y;
          if(this.isLevel){
            x = ev.clientX - this.$refs.cus_progress.offsetLeft - 40;
            progressPellet = x / this.width * 100 +'%';
            currentTime = x / this.width;
          }else{
            y = ev.clientY - this.$refs.cus_progress.offsetTop - 40;
            progressPellet = y / this.height * 100 +'%';
            currentTime = y / this.height;
          };
          this.$emit('progressDown',{
            progressPellet: progressPellet,
            currentTime: currentTime,
          });
          return false;
        }
         })
    }
  }
</script>
<style lang="less">
  .cus_progress{
    background-color: #E6E6E8;
    margin: 0 auto;
    position: relative;
    border-radius: 2px;
    >.progress_pellet{
      position: absolute;
      left: 0;
      top: -5px;
      bottom: 0;
      margin: auto;
      display: inline-block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: #fff;
      z-index: 1;
      transition: left 200ms ease;
    }
    >.progress_bar{
      position: absolute;
      left: 0;
      top: 0;
      width: 0;
      height: 4px;
      border-radius: 2px;
      background-color: #E83C3C;
      transition: width 200ms ease;
    }
    &:hover{
      cursor: pointer;
    }
  }
</style>




